import { _decorator, Component, Node } from "cc";
const { ccclass, property } = _decorator;

@ccclass("ScoreHandler")
export class ScoreHandler extends Component {
  @property(Number) score: number = 0;
  @property(Number) incrementInterval: number = 1; // İncrement aralığı (saniye)

  private elapsedTime: number = 0;

  protected update(dt: number) {
    this.elapsedTime += dt;
    if (this.elapsedTime >= this.incrementInterval) {
      this.elapsedTime -= this.incrementInterval; // Süreyi sıfırla
      this.incrementScore();
    }
  }

  private incrementScore() {
    this.score += 1;
    console.log("Score: ", this.score); // Skoru kontrol etmek için
  }

  public getScore() {
    return this.score;
  }

  //   public addScore(currentscore: number, value: number) {
  //     return (currentscore += value);
  //   }
}
